import React from "react";
import image from "../../assests/hotwheels.png";

import "./index.css";

const NavBar = () => {
  return (
    <nav className="container-navbar">
      <div className="logo">
        <img
          src={image}
          alt="HotWheels logo"
          width={90}
          height={80}
          loading="eager"
        />
      </div>
      <ul className="navBar">
        <li>
          <a href="#home">Home</a>
        </li>
        <li>
          <a href="#cars">Cars</a>
        </li>
        <li>
          <a href="#about">About</a>
        </li>
      </ul>
    </nav>
  );
};

export default NavBar;
