import React, { useState } from "react";
import { listCarros } from "../../data";
import "./index.css";

export const AddCar = () => {
  const [name, setName] = useState("");
  const [marca, setMarca] = useState("");
  const [color, setColor] = useState("");
  const [date, setDate] = useState("");

  const [success, setSuccess] = useState(true);
  const [canceled, setCanceled] = useState(true);

  const [error, setError] = useState(true);

  const handleClickAdd = () => {
    const object = {
      name,
      marca,
      color,
      date,
    };

    if (checkedInput(object)) {
      listCarros.push(object);
      setName("");
      setMarca("");
      setColor("");
      setDate("");
      setSuccess(false);
      setInterval(() => setSuccess(true), 5000);
    } else {
      setError(false);
      setInterval(() => setError(true), 1000);
    }
  };

  const handleClickDelete = () => {
    setName("");
    setMarca("");
    setColor("");
    setDate("");
    setCanceled(false);
    setInterval(() => setCanceled(true), 1000);
  };

  const checkedInput = (object) => {
    for (let item of Object.values(object)) {
      if (item === "") {
        return false;
      }
    }
    return true;
  };

  return (
    <div className="div-container">
      <div>
        <label for="name">Nome:</label>
        <input
          name="name"
          value={name}
          onChange={(e) => setName(e.target.value)}
          required
        />
      </div>
      <div>
        <label for="marca">Marca:</label>
        <input
          name="marca"
          value={marca}
          onChange={(e) => setMarca(e.target.value)}
          required
        />
      </div>
      <div>
        <label for="color">Cor:</label>
        <input
          name="color"
          value={color}
          onChange={(e) => setColor(e.target.value)}
          required
        />
      </div>
      <div>
        <label for="ano">Ano:</label>
        <input
          name="ano"
          type="date"
          value={date}
          onChange={(e) => setDate(e.target.value)}
          required
        />
      </div>

      <button onClick={() => handleClickAdd()}>Adicionar</button>
      <button onClick={() => handleClickDelete()}>Cancelar</button>
      <div>
        <p className="success-msg" hidden={success}>
          Adicionado com sucesso!
        </p>
        <p className="canceled-msg" hidden={canceled}>
          Cancelado!
        </p>
        <p className="canceled-msg" hidden={error}>
          Dados inválidos,
          <br /> Preencha todos os campos.
        </p>
      </div>
    </div>
  );
};
