import React, { useState, useEffect } from "react";

import "./index.css";
import { listCarros } from "../../data";

export const CarDelete = () => {
  const updateList = listCarros;
  const [data, setData] = useState([]);

  const [success, setSuccess] = useState(true);

  useEffect(() => {
    setData(listCarros);
  }, [updateList, success]);

  const handleClick = (index) => {
    listCarros.splice(index, 1);
    setSuccess(false);
    setInterval(() => setSuccess(true), 1000);
  };
  return (
    <div>
      {data &&
        data.map((element, index) => (
          <div className="container-item-colection">
            <p className="list-name">Nome: {element.name}</p>
            <p className="list-name">Marca: {element.marca}</p>
            <p className="list-name">cor: {element.cor}</p>
            <p className="list-name">Ano: {element.date}</p>
            <button className="primary" onClick={() => handleClick(index)}>
              Excluir
            </button>
          </div>
        ))}
      {data.length <= 0 && (
        <p className="list-empty">Não há carros a serem deletados.</p>
      )}
    </div>
  );
};
